import { useNavigate } from 'react-router-dom';
import './breadcrumbs.sass'
import { Link } from 'react-router-dom';

const Breadcrumbs = () => {

    const navigator = useNavigate()

    return(
        <nav className='breadcrumbs'>
            <div className="container">
                <ol className='breadcrumbs__list'>
                    <Link to={'/'} className='breadcrumbs__item'></Link>
                    <Link to={'/catalog'} className='breadcrumbs__item current'></Link>
                    <Link to={'/cart'} className='breadcrumbs__item current'></Link>
                    {/* <Link to={'/'} className='breadcrumbs__item'></Link> */}
                    {/* <li className='breadcrumbs__item current'>BioMio BIO-SOAP   .   , 90 </li> */}
                </ol>
            </div>
        </nav>
    )
}

export default Breadcrumbs;